import React, { Component } from 'react';
import Header from './HeaderComponent';
import Footer from './FooterComponent';
import Course from './CourseComponent';
import Home from './HomeComponent';
import About from './AboutComponent';
import Contact from './ContactComponent';
import CourseDetail from './CoursedetailComponent';
import { Switch, Route, Redirect, withRouter } from 'react-router-dom'
import { connect } from 'react-redux';
import { actions } from 'react-redux-form';
import { postComment,loginUser, logoutUser,  googleLogin,postBookSlot,fetchTeachers, fetchCourses, fetchComments, postFeedback } from '../redux/ActionCreators';







const mapStateToProps = state => {
    return {
        courses: state.courses,
        teachers: state.teachers,
        comments: state.comments,
        auth: state.auth
    }
}

const mapDispatchToProps = dispatch => ({
    postComment: (courseId, rating, comment) => dispatch(postComment(courseId, rating, comment)),
    fetchCourses: ()=>{dispatch(fetchCourses())},
    fetchTeachers: () => {dispatch(fetchTeachers())},
    fetchComments: ()=>{dispatch(fetchComments())},
    resetFeedbackForm: () => { dispatch(actions.reset('feedback'))},
    resetBookSlotForm: () => { dispatch(actions.reset('bookSlot'))},
    postFeedback: (feedback) => dispatch(postFeedback(feedback)),
    postBookSlot: (bookSlot) => dispatch(postBookSlot(bookSlot)),
    loginUser: (creds) => dispatch(loginUser(creds)),
    logoutUser: () => dispatch(logoutUser()),
    googleLogin: () => dispatch(googleLogin())
});

class Main extends Component {

    constructor(props) {
        super(props);
    }
    componentDidMount() {
        this.props.fetchCourses();
        this.props.fetchTeachers();
        this.props.fetchComments();
    }
    componentWillUnmount() {
        this.props.logoutUser();
      }

    render() {
        const HomePage = () => {
            return (
                <Home
                    course={this.props.courses.courses.filter((course)=>course.featured)}
                    courseLoading = {this.props.courses.isLoading}
                    courseErrMess={this.props.courses.errMess}
                />
            );
        }
        const CourseWithId = ({match}) => {
            return(
              (this.props.auth.isAuthenticated)
              ?
              <CourseDetail course={this.props.courses.courses.filter((course) => course._id === match.params.courseId)[0]}
                isLoading={this.props.courses.isLoading}
                errMess={this.props.courses.errMess}
                comments={this.props.comments.comments.filter((comment) => comment.course === match.params.courseId)}
                commentsErrMess={this.props.comments.errMess}
                postComment={this.props.postComment}
                
                />
              :
              <CourseDetail course={this.props.courses.courses.filter((course) => course._id === match.params.courseId)[0]}
                isLoading={this.props.courses.isLoading}
                errMess={this.props.courses.errMess}
                comments={this.props.comments.comments.filter((comment) => comment.course === match.params.courseId)}
                commentsErrMess={this.props.comments.errMess}
                postComment={this.props.postComment}
              
                />
            );
          }


        return (
            <div>
                 <Header auth={this.props.auth} 
                    loginUser={this.props.loginUser} 
                    logoutUser={this.props.logoutUser}
                    googleLogin={this.props.googleLogin}
                />   
                        <Switch>
                            <Route path='/home' component={HomePage} />
                            <Route path='/aboutus' component={() => <About teachers={this.props.teachers} />} />
                            <Route exact path='/courses' component={() => <Course courses={this.props.courses} />} />
                            <Route path='/courses/:courseId' component={CourseWithId} />
                            <Route exact path='/contactus' component={() => <Contact resetFeedbackForm={this.props.resetFeedbackForm} postFeedback={this.props.postFeedback}/>} />
                            <Redirect to='/home' />
                        </Switch>       
                <Footer />
            </div>
        );
    }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Main));